.App {
  padding:0px;
  background-color: #fff;
  overflow: hidden;
  
}

h1, h2 {
  color: #333;
}

label {
  display: block;
  margin-bottom: 10px;
  font-size: 16px;
  color: #555;
}

input {
  width: 100%;
  padding: 12px;
  margin-top: 5px;
  margin-bottom: 15px;
  box-sizing: border-box;
  text-transform: uppercase;
  border-radius: 8px;
  font-size: 16px;
  outline: none;
  transition: border-color 0.3s;
}

input:focus {
  border-color: #27ae60;
}

button {
  background-color: #232323;
  color: #fff;
  padding: 15px 25px;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #00000072;
}

p {
  margin: 15px 0;
  font-size: 16px;
}

.loading {
  text-align: center;
  margin: 20px;
  font-size: 18px;
  color: #555;
}

.CertificateDetail {
