/* ErrorPopup component styling */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.modal {
  max-width: 400px;
  width: 100%;
  text-align: center;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #fff;
  z-index: 1000;
}

.custom-error-modal {
  display: flex;
  flex-direction: column;
}

.custom-error-modal-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.custom-error-modal-button {
  background-color: #232323;
  color: #fff;
  padding: 10px 15px;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  margin-top: 10px;
  transition: background-color 0.3s;
}

.custom-error-modal-button:hover {
  background-color: #00000072;
}

